.pcui-curve {
    display: inline-block;
    flex: 1;
    height: 24px;
    box-sizing: border-box;
    margin: $element-margin;
    transition: opacity 100ms, box-shadow 100ms;

    border: 1px solid $bcg-darker;
    background-color: $bcg-dark;

    > canvas {
        width: 100%;
        height: 100%;
        background-color: transparent;
    }
}

.pcui-curve-picker {
    border: 1px #2a3538 solid;
}

.pcui-curve-picker-toggles {
    position: absolute;
    right: 0;
}

.pcui-curve-picker-toggles.active {
    background-color: black;
}

.pcui-curve-picker-header {
    display: flex;
    align-items: center;
    font-size: 12px;
    width: 470px !important;

    > .pcui-select-input {
        font-size: 12px;
        margin-right: 25px;
        min-width: 125px;
    }
}

.pcui-curve-picker-canvas {
    width: 470px !important;
    height: 200px !important;
    background-color: #2a3538 !important;
}

.pcui-curve-picker-footer {
    display: flex;
    align-items: center;
    width: 470px !important;

    > .pcui-button {
        min-width: 30px;
    }
}

// disabled gradient input
.pcui-curve.pcui-disabled, .pcui-curve.pcui-multiple-values {
    opacity: $element-opacity-disabled;
}

// hover / focus states
.pcui-curve:not(.pcui-disabled):not(.pcui-readonly):not(.pcui-multiple-values) {
    &:hover {
        cursor: pointer;
    }

    &:hover, &:focus {
        cursor: pointer;
        box-shadow: $element-shadow-hover;
    }
    &:active {
        box-shadow: $element-shadow-active;
    }
}
